:root {
  --description_font_size: 180px;
  --description_sub_font_size: 30px;
}

.description_background {
  width: 100%;
  height: 100%;
}

.understand_section {
  width: 100%;
  height: 50%;
  background-color: #4a951d;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}

.center {
  width: 80%;
  height: 80%;
}

.understand_title {
  margin: 0;
}

.understand_title p {
  margin: 0;
  text-align: center;
  font-size: var(--description_font_size);
  font-weight: 700;
  line-height: 1;
}

.understand_sub_title {
  margin: 0;
  margin-top: 40px;
  text-align: center;
  font-size: var(--description_sub_font_size);
  font-weight: 500;
}

.message_section {
  width: 100%;
  height: 50%;
  background-color: #092b4b;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}

.message_title {
  margin: 0;
}

.message_title p {
  margin: 0;
  text-align: center;
  font-size: var(--description_font_size);
  font-weight: 700;
  line-height: 1;
}

.message_sub_title {
  margin: 0;
  margin-top: 40px;
  text-align: center;
  font-size: var(--description_sub_font_size);
  font-weight: 500;
}
