:root {
  --introduction_font_size: 80px;
}

.introduction_background {
  background-color: #ffffff;
  height: 100%;
  display: flex;
}

.introduction_center {
  width: 80%;
  height: 80%;
  margin: auto;
  display: flex;
  align-items: center;
}

.introduction_center p {
  width: 100%;
  margin: 0;
  font-size: var(--introduction_font_size);
  text-align: center;
}
