:root {
  --development_font_size: 100px;
  --development_sub_font_size: 25px;
}

.development_background {
  background-color: #000000;
  height: 100%;
  display: flex;
}

.development_center {
  width: 80%;
  height: 80%;
  margin: auto;
}

.title {
  width: 100%;
  height: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.title p {
  margin: 0;
  color: #ffffff;
  font-size: var(--development_font_size);
  font-weight: 700;
}

.contact {
  width: 100%;
  height: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact p {
  margin: 0;
  color: #ffffff;
  font-size: var(--development_sub_font_size);
  font-weight: 300;
}

.app_store {
  width: 100%;
  height: 50%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.app_sore_img_1 {
  margin-right: 70px;
}
