:root {
  --header_font_size: 220px;
}

.header_background {
  background-color: #b84545;
  height: 100%;
  display: flex;
}

.header_center {
  padding: 0;
  width: 80%;
  height: 80%;

  margin: auto;
}

.header_font {
  padding: 0;
  margin: 0;
  color: #ffffff;
  font-size: var(--header_font_size);
  text-align: center;
  font-weight: 800;
  letter-spacing: 12px;
  line-height: 1.1;
}
